import React from 'react';
import { Col, Row } from 'react-bootstrap';
import { ParallaxBanner, Parallax } from 'react-scroll-parallax';
import './index.css';

import perso2 from '../../assets/perso1.jpeg';
import perso1 from '../../assets/perso2.jpeg';
import characters from '../../assets/characters.jpeg';


const History = () => {

  return (

    <div className="history-app">
      
      <ParallaxBanner
        layers={[
          {
              image: characters,
              amount: 0.1,
          },
        ]}
        style={{
            width: '100%',
            height: '50vw',
            position: "relative",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
        }}
      >

        <div className="history-text-container">

          <p className="history-text-title"> Collect your favorite characters </p>
        

        <Row style={{padding: 0, margin: 0}}>
            
            <Col lg={6} md={6} sm={12} xs={12} style={{padding: 0, margin: 0}}>
              <Parallax tagOuter="history-perso1" y={[30, -30]}>
                <div className="history-container-left">
                  <img alt="" className="history-img" src={perso1} />
                </div>
              </Parallax>
            </Col>
    
            <Col lg={6} md={6} sm={12} xs={12} style={{padding: 0, margin: 0}} >
              <Parallax tagOuter="history-perso2" y={[-30, 30]}>
                <div className="history-container-right">
                  <img alt="" className="history-img" src={perso2} />
                </div>
              </Parallax>
            </Col>
    
          </Row>

        </div> 

      </ParallaxBanner>

    </div>

  );
};

export default (History);
