import './index.css';
import React, { Suspense, useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { Col, Row } from 'react-bootstrap';


const TermsPopup = () => {

  const [visible, setVisible] = React.useState(false);

    useEffect(()=>{

      let pop_status = localStorage.getItem('pop_status');
      if(!pop_status){
        setVisible(true);
        localStorage.setItem('pop_status',0);
      }
      if(pop_status == 0){
        setVisible(true);
        localStorage.setItem('pop_status',0);
      }
    },[])

    if(!visible) 
        return null;

    return (

        <div className="termspopup-app">
          
          <div className="termspopup-container">

            <Row style={{padding: 0, margin: 0}}>
              
              <Col lg={4} md={4} sm={12} xs={12} style={{padding: 0, margin: 0}}>
                <div className="termspopup-container-col">
                  <p className="termspopup-text-title"> Your privacy </p>
                </div>
              </Col>

              <Col lg={5} md={5} sm={12} xs={12} style={{padding: 0, margin: 0}}>
                <div className="termspopup-container-col">
                  <p className="termspopup-text-description">
                    We use cookies to improve your experience on our website. To find out more, read our &nbsp;
                    <Link to="/terms" style={{textDecoration: "none", marginRight: "5%"}}>
                      cookie policy.
                    </Link>
                  </p>
                </div>
              </Col>

              <Col lg={3} md={3} sm={12} xs={12} style={{padding: 0, margin: 0}}>
                <div className="termspopup-container-col">
                  <p onClick={() => {localStorage.setItem('pop_status',1); setVisible(false);} } className="termspopup-button ">
                    OK !
                  </p>
                </div>
              </Col>

            </Row>
            
          </div>  

        </div>
    )
}


export default (TermsPopup);